<template>
  <div>
    <headerBar :header-data="headerData" />
    <div class="container-tab">
      <van-notice-bar
        close
        left-icon="volume-o"
        text="1、如果填写非当天的工作日志，注意选择“日期”；2、加班工作正常填写到对应类型，前面加上“加班”关键字。"
      />

      <div class="common-main box" style="border-radius: 0;padding: 0 10px;">
        <van-cell title="选择日期" :value="date" is-link @click="show = true" />
        <van-calendar v-model="show" :min-date="minDate" color="#1890ff" @confirm="onConfirm" />
      </div>

      <div class="content">
        <div class="common-main box">
          <van-cell title="当前工作量(小时)" :value="getDqgzl()" />
          <van-cell title="剩余(小时)" :value="getSysj()" />
          <!-- <van-field v-model="value" label-width="100" label="备注" input-align="right" placeholder="请输入备注内容" /> -->
        </div>
      </div>
      <div v-if="list && list.length>0 && list[0]">
        <!-- 循环列表 begin -->
        <van-swipe-cell v-for="(item,index) of list" :key="index" :disabled="isSubmit || isgd || item.zt===1">
          <div class="content">
            <div class="common-main box">
              <van-cell v-model="item.gzxzmc" title="分类" is-link @click="showXzflPopup(index,item)" />
              <van-cell v-show="item.showXm || item.xmmc!=='请选择项目' && item.xmmc" v-model="item.xmmc" title="项目" is-link @click="goXzGzrzXm(index,item)" />
              <div class="page-list block">
                <div class="pagelist-left">内容</div>
                <div class="pagelist-right">
                  <van-field
                    v-model="item.gznr"
                    rows="2"
                    autosize
                    type="textarea"
                    maxlength="100"
                    placeholder="请输入日志内容"
                    show-word-limit
                    :readonly="isSubmit || isgd || item.zt===1"
                  />
                <!-- <van-field
                  v-model="item.gznr"
                  v-validate="'required'"
                  rows="2"
                  autosize
                  name="rznr"
                  :error-message="errors.first('rznr')"
                  type="textarea"
                  placeholder="请输入日志内容"
                  show-word-limit
                /> -->
                </div>
              </div>
              <van-field
                v-model="item.gzl"
                input-align="right"
                label="工作量(小时)"
                placeholder="请输入工作量"
                type="number"
                :readonly="isSubmit || isgd || item.zt===1"
              />
            <!-- <van-field
              v-model="item.gzl"
              v-validate="'required|gzlCheck'"
              name="gzl"
              :error-message="errors.first('gzl')"
              input-align="right"
              label="工作量(小时)"
              placeholder="请输入工作量"
              type="number"
            /> -->
            </div>
          </div>
          <template #right>
            <div class="sideslip">
              <div class="sideslip-item red">
                <div class="icon" @click="delGzrz(index)"> <i class="iconfont iconshanchu1" /> </div> 删除
              </div>
            </div>
          </template>
        </van-swipe-cell>
      <!-- 循环列表 end -->
      </div>
      <!-- 增加日志按钮 begin -->
      <div v-if="!isSubmit" class="foot-btn-white">
        <van-button type="info" block plain size="large" @click="addGzrz()"><i class="iconfont iconzengjia" /> 增加工作日志</van-button>
      </div>
      <!-- 增加日志按钮 end -->

    </div>

    <!-- 选择日志分类弹窗 beign -->
    <van-popup
      v-model="XzflShow"
      position="bottom"
      closeable
      round
      :style="{ height: '65%' }"
    >
      <div v-if="list && list.length>0 && list[0]">
        <div class="common-popup">
          <div class="popup-title">选择考勤类型</div>
          <div class="popup-content">
            <div class="select-list-flex">
              <ul v-if="list && list.length>0">
                <li v-for="(item,index) of kqlxList" :key="index" :class="rzlxIndex===index?'active':''" @click="kqlxClick(item,index)">{{ item.gzxzmc }}</li>
              </ul>
            </div>
          </div>
        <!-- <div class="foot-btn fixed">
          <ul>
            <li><van-button type="info" round size="large" @click="xzflQd()">确定</van-button></li>
          </ul>
        </div> -->
        </div>
      </div>
    </van-popup>
    <!-- 选择日志分类弹窗 end -->

    <div class="common-space" />
    <div v-show="Native_ShowSoftKeyBoard" class="common-space2" />
    <!-- 底部按钮 begin -->
    <div class="foot-btn full fixed">
      <ul>
        <li>
          <div class="foot-option" @click="goCkGzrz()">
            <div class="foot-item">
              <i class="iconfont iconxiangqing" />
              查看我的工作日志
            </div>
          </div>
        </li>
        <!-- <li v-if="!isSubmit"><van-button type="info" round size="large" @click="submit(false)">保存</van-button></li> -->
        <li v-if="spzt!==2"><van-button type="info" round size="large" @click="submit(true)">{{ isSubmit?'取回':'提交' }}</van-button></li>
      </ul>
    </div>
    <!-- 底部按钮 end -->

  </div>
</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar.vue'
import { getUserName } from '@/common/js/catch'
import { pageRouter, NativeCaJS } from '@/common/js/mixin.js'

export default {
  name: 'TbGzrz',
  components: {
    headerBar
  },
  mixins: [pageRouter, NativeCaJS],
  data() {
    return {
      isSubmit: false,
      headerData: {
        title: '填报工作日志',
        leftUrl: 'RzIndex',
        leftText: ' '
      },
      date: '请选择日期',
      minDate: new Date(1999, 0, 1),
      show: false,
      XzflShow: false,
      kqlxList: [],
      list: [],
      rq: '',
      rzlxIndex: 0,
      buttonState: true,
      rzCheckedIndex: 0, // 选中日志下标
      spzt: 0,
      isgd: false // 当前日期日志是否归档
    }
  },
  activated() {
    if (this.$store.state.erp.isFirst) {
      this.$store.commit('erp/SET_ISFIRST', false)
      let nowDate = new Date()
      if (nowDate.getDay() === 1) {
        var time3 = (new Date()).getTime() - 3 * 24 * 60 * 60 * 1000
        nowDate = new Date(time3) // 获取的是前3天日期
      } else if (nowDate.getDay() === 0) {
        var time1 = (new Date()).getTime() - 2 * 24 * 60 * 60 * 1000
        nowDate = new Date(time1) // 获取的是前2天日期
      } else {
        var time2 = (new Date()).getTime() - 24 * 60 * 60 * 1000
        nowDate = new Date(time2) // 获取的是前一天日期
      }
      this.getZtRzxx(nowDate)
    }
    this.isSubmit = this.$store.state.erp.isSubmit
    if (this.$store.state.erp.list && this.$store.state.erp.list.length > 0) {
      this.list = this.$store.state.erp.list
      if (this.list.length > 0) {
        this.list.splice(0, 1, this.list[0])
      }
    }
    if (this.$store.state.erp.rq && this.$store.state.erp.rq !== '请选择日期' && this.$store.state.erp.rq !== '') {
      this.rq = this.$store.state.erp.rq
      this.date = this.$store.state.erp.rq1
      this.getIsGdByDate1(this.rq)
    }
    if (this.$store.state.erp.checkIndex) {
      this.rzCheckedIndex = this.$store.state.erp.checkIndex
    }
    if (this.$store.state.erp.checkXm) {
      this.list[this.rzCheckedIndex].xmmc = this.$store.state.erp.checkXm.xmmc
      this.list[this.rzCheckedIndex].xm = this.$store.state.erp.checkXm.id
    }
    this.getRzxz()
  },
  created() {
    if (this.$store.state.erp.isFirst) {
      this.$store.commit('erp/SET_ISFIRST', false)
      let nowDate = new Date()
      if (nowDate.getDay() === 1) {
        var time3 = (new Date()).getTime() - 3 * 24 * 60 * 60 * 1000
        nowDate = new Date(time3) // 获取的是前3天日期
      } else if (nowDate.getDay() === 0) {
        var time1 = (new Date()).getTime() - 2 * 24 * 60 * 60 * 1000
        nowDate = new Date(time1) // 获取的是前2天日期
      } else {
        var time2 = (new Date()).getTime() - 24 * 60 * 60 * 1000
        nowDate = new Date(time2) // 获取的是前一天日期
      }
      this.getZtRzxx(nowDate)
    }
    this.isSubmit = this.$store.state.erp.isSubmit
    if (this.$store.state.erp.list && this.$store.state.erp.list.length > 0) {
      this.list = this.$store.state.erp.list
      this.list.splice(0, 1, this.list[0])
    }
    if (this.$store.state.erp.rq && this.$store.state.erp.rq !== '请选择日期' && this.$store.state.erp.rq !== '') {
      this.rq = this.$store.state.erp.rq
      this.date = this.$store.state.erp.rq1
      this.getIsGdByDate1(this.rq)
    }
    if (this.$store.state.erp.checkIndex) {
      this.rzCheckedIndex = this.$store.state.erp.checkIndex
    }
    if (this.$store.state.erp.checkXm) {
      this.list[this.rzCheckedIndex].xmmc = this.$store.state.erp.checkXm.xmmc
      this.list[this.rzCheckedIndex].xm = this.$store.state.erp.checkXm.id
    }
    this.getRzxz()
  },
  methods: {
    /* 参数类型String */
    getIsGdByDate1(nowDate) {
      this.$store.dispatch('erp/isGdByDateAction', {
        date: nowDate
      }).then(res => {
        if (res.state === 1) {
          this.isgd = false
        } else {
          this.isgd = true
        }
      }).catch(error => {
        console.log(error)
      })
    },
    /* 参数类型Time */
    getIsGdByDate(nowDate) {
      this.$store.dispatch('erp/isGdByDateAction', {
        date: this.formatDate1(nowDate)
      }).then(res => {
        if (res.state === 1) {
          this.isgd = false
        } else {
          this.isgd = true
        }
      }).catch(error => {
        console.log(error)
      })
    },
    getDqgzl() {
      if (!this.list || this.list.length === 0) {
        return '0.00'
      } else {
        let num = 0
        for (let i = 0; i < this.list.length; i++) {
          if (this.list[i] && this.list[i].gzl) {
            num += parseFloat(this.list[i].gzl)
          }
        }
        return num !== 0 ? num.toFixed(2) : '0.00'
      }
    },
    getZtRzxx(nowDate) {
      const begin = this.formatDate1(nowDate)
      this.$store.dispatch('erp/getGzrzAction', {
        ksrq: begin,
        jzrq: begin,
        ryid: getUserName(),
        currentPage: 1,
        shbz: [0, 1, 2, 3]
      }).then(res => {
        res = res.gzrzArr
        if (res && res[0] && res[0].rzmxArr && res[0].rzmxArr.length > 0 && res[0].spzt !== 0) {
          this.date = this.formatDate(new Date())
          this.rq = this.formatDate1(new Date())
          this.getIsGdByDate(new Date())
          this.getRzxx()
          return
        } else {
          this.getIsGdByDate(nowDate)
          this.date = this.formatDate(nowDate)
          this.rq = this.formatDate1(nowDate)
          if (!res || res === [] || res.length === 0) {
            this.list = []
            this.$store.commit('erp/SET_LIST', [])
            this.$store.commit('erp/SET_ISSUBMIT', false)
            this.isSubmit = false
            return
          }
          this.list = res[0].rzmxArr
          this.$store.commit('erp/SET_LIST', this.list)
          // if (res && res.length > 0 && res[0].rzmxArr && res[0].rzmxArr.length > 0 && res[0].spzt !== 0) {
          //   this.isSubmit = true
          //   this.$store.commit('erp/SET_ISSUBMIT', true)
          // } else {
          this.$store.commit('erp/SET_ISSUBMIT', false)
          this.isSubmit = false
          // }
        }
        for (let i = 0; i < this.list.length; i++) {
          if (this.list[i].xmmc === '请选择项目') {
            this.list[i].showXm = false
          }
        }
      }).catch(error => {
        this.$toast(error)
      })
    },
    getRzxx() {
      this.$store.dispatch('erp/getGzrzAction', {
        ksrq: this.rq,
        jzrq: this.rq,
        ryid: getUserName(),
        currentPage: 1,
        shbz: [0, 1, 2, 3]
      }).then(res => {
        res = res.gzrzArr
        if (res && res[0]) {
          this.spzt = res[0].spzt
        } else {
          this.spzt = 0
        }
        if (!res || res === [] || res.length === 0) {
          this.list = []
          this.$store.commit('erp/SET_LIST', [])
          this.$store.commit('erp/SET_ISSUBMIT', false)
          this.isSubmit = false
          return
        }
        this.list = res[0].rzmxArr ? res[0].rzmxArr : []
        this.$store.commit('erp/SET_LIST', this.list)
        if (res && res.length > 0 && res[0].rzmxArr && res[0].rzmxArr.length > 0 && res[0].spzt !== 0) {
          this.isSubmit = true
          this.$store.commit('erp/SET_ISSUBMIT', true)
          for (let i = 0; i < this.list.length; i++) {
            if (this.list[i].xmmc === '请选择项目') {
              this.list[i].showXm = false
            }
          }
          if (this.list.length > 0) {
            this.list.splice(0, 1, this.list[0])
          }
        } else {
          this.$store.commit('erp/SET_ISSUBMIT', false)
          this.isSubmit = false
        }
      }).catch(error => {
        this.$toast(error)
      })
    },
    submit(state) {
      if (!this.buttonState) {
        return
      }
      this.buttonState = false
      if (this.isgd) {
        this.$toast('所选日期日志已归档，无法编写日志！')
        this.buttonState = true
        return
      }
      if (this.isSubmit) {
        this.$store.dispatch('erp/getBackRzAction', {
          ryid: getUserName(),
          rq: this.rq
        }).then(res => {
          this.buttonState = true
          this.$toast('日志取回成功！')
          this.isSubmit = false
        })
        return
      }
      this.$validator.validate().then(result => {
        let state1 = false
        let state2 = false
        let state3 = false
        let state4 = false
        let gzlzh = 0
        for (let i = 0; i < this.list.length; i++) {
          if (!this.list[i].gzxzmc || this.list[i].gzxzmc === '请选择分类') {
            state1 = true
            break
          } else {
            state1 = false
          }
          if ((!this.list[i].xm || this.list[i].xmmc === '请选择项目') && this.list[i].showXm) {
            state2 = true
            break
          } else {
            state2 = false
          }
          if (!this.list[i].gznr) {
            state3 = true
            break
          } else {
            state3 = false
          }
          if (!this.list[i].gzl) {
            state4 = true
            break
          } else {
            if (this.list[i].gzl <= 0) {
              this.$toast('请输入正确工作量！')
              this.buttonState = true
              return
            }
            if (this.list[i].gzl > 24) {
              this.$toast('请输入正确工作量！')
              this.buttonState = true
              return
            }
            gzlzh += parseFloat(this.list[i].gzl)
            state4 = false
          }
        }
        if (!this.rq && this.rq !== '请选择日期') {
          this.$toast('请选择日期！')
          this.buttonState = true
          return
        }
        if (this.list.length === 0) {
          this.$toast('请先增加工作日志明细！')
          this.buttonState = true
          return
        }
        if (state1) {
          this.$toast('请选择分类!')
          this.buttonState = true
          return
        } else {
          if (state2) {
            this.$toast('请选择项目!')
            this.buttonState = true
            return
          } else {
            if (state3) {
              this.$toast('请输入日志内容！')
              this.buttonState = true
              return
            } else {
              if (state4) {
                this.$toast('请输入工作量！')
                this.buttonState = true
                return
              }
              for (let i = 0; i < this.list.length; i++) {
                this.list[i].sxh = i + 1
              }
              if (gzlzh > 24) {
                this.$toast('总工作量不能大于24小时！')
                this.buttonState = true
                return
              }
              const list1 = JSON.parse(JSON.stringify(this.list))
              for (let i = 0; i < list1.length; i++) {
                if (list1[i].xmmc === '请选择项目') {
                  list1[i].xmmc = ''
                  list1[i].xm = ''
                }
              }
              this.$store.dispatch('erp/saveGzrzAction', {
                rq: this.rq,
                ryid: getUserName(),
                submit: state,
                rzmxArr: list1
              }).then(res => {
                this.buttonState = true
                if (state) {
                  this.$toast('提交日志成功！')
                  this.isSubmit = true
                } else {
                  this.$toast('保存日志成功！')
                  this.isSubmit = false
                }
              }).catch(error => {
                this.buttonState = true
                this.$toast(error)
              })
            }
          }
        }
      })
    },
    xzflQd() {
      this.$store.commit('erp/SET_LIST', this.list)
      this.XzflShow = false
    },
    getSysj() {
      if (!this.list || !this.list[0]) {
        return '7.50'
      }
      let num = 7.50
      for (let i = 0; i < this.list.length; i++) {
        if (this.list[i].gzl) {
          num = num - this.list[i].gzl
        }
      }
      return num.toFixed(2)
    },
    delGzrz(index) {
      if (index === this.rzCheckedIndex) {
        this.rzCheckedIndex = this.rzCheckedIndex - 1
      }
      this.list.splice(index, 1)
      this.$store.commit('erp/SET_LIST', this.list)
    },
    addGzrz() {
      if (this.isgd) {
        this.$toast('所选日期日志已归档，无法编写日志！')
        return
      }
      this.list.push({
        xm: '',
        gzxzmc: '请选择分类',
        gzxzbm: '',
        xmmc: '请选择项目',
        gzl: '',
        gznr: ''
      })
      this.$store.commit('erp/SET_LIST', this.list)
    },
    kqlxClick(kqlx, index) {
      this.list[this.rzCheckedIndex].gzxzmc = kqlx.gzxzmc
      this.list[this.rzCheckedIndex].gzxzbm = kqlx.gzxzbs
      this.list[this.rzCheckedIndex].showXm = kqlx.glxm
      this.list[this.rzCheckedIndex].xmmc = '请选择项目'
      this.list[this.rzCheckedIndex].rzlxIndex = index
      if (this.list.length > 0) {
        this.list.splice(0, 1, this.list[0])
      }
      this.$store.commit('erp/SET_LIST', this.list)
      this.XzflShow = false
    },
    // 获取工作性质分类
    getRzxz() {
      this.$store.dispatch('erp/getRzxzAction').then(res => {
        this.kqlxList = res
      }).catch(error => {
        this.$toast(error)
      })
    },
    formatDate(date) {
      return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`
    },
    formatDate1(date) {
      const year = date.getFullYear()
      let month = ''
      let day = ''
      if ((date.getMonth() + 1) < 10) {
        month = '0' + (date.getMonth() + 1)
      } else {
        month = date.getMonth() + 1
      }
      if (date.getDate() < 10) {
        day = '0' + (date.getDate())
      } else {
        day = date.getDate()
      }
      return year + '' + month + day
    },
    onConfirm(date) {
      this.show = false
      this.rq = this.formatDate1(date)
      this.date = this.formatDate(date)
      this.$store.commit('erp/SET_RQ', this.rq)
      this.$store.commit('erp/SET_RQ1', this.date)
      this.getRzxx()
      this.getIsGdByDate(date)
    },
    // 选择日志分类
    showXzflPopup(index, item) {
      if (this.isSubmit) {
        return
      }
      if (this.isgd) {
        this.$toast('所选日期日志已归档，无法编写日志！')
        return
      }
      if (item.zt === 1) {
        this.$toast('当前项目已归档，无法编写日志！')
        return
      }
      this.rzCheckedIndex = index
      this.rzlxIndex = this.list[index].rzlxIndex ? this.list[index].rzlxIndex : 0
      this.$store.commit('erp/SET_CHECKINDEX', index)
      this.XzflShow = true
    },
    goCkGzrz() {
      if (!this.isSubmit && this.list.length > 0) {
        this.$toast('请先提交当前日志！')
        return
      }
      this.$store.commit('erp/SET_RQ', this.rq)
      this.$store.commit('erp/SET_RQ1', this.date)
      this.$store.commit('erp/SET_ISSUBMIT', this.isSubmit)
      this.$router.push({
        name: 'CkGzrz'
      })
    },
    goXzGzrzXm(index, item) {
      if (this.isSubmit) {
        return
      }
      if (this.isgd) {
        this.$toast('所选日期日志已归档，无法编写日志！')
        return
      }
      if (item.zt === 1) {
        this.$toast('当前项目已归档，无法编写日志！')
        return
      }
      this.$store.commit('erp/SET_ISSUBMIT', this.isSubmit)
      this.$store.commit('erp/SET_RQ', this.rq)
      this.$store.commit('erp/SET_RQ1', this.date)
      this.rzCheckedIndex = index
      this.$store.commit('erp/SET_CHECKINDEX', index)
      this.$router.push({
        name: 'XzGzrzXm'
      })
    }
  }
}
</script>

<style scoped>
.van-cell::after{display: none;}
.focusState {position: absolute;left: 0;right: 0;top:-200px;}
.common-space2{height: 200px;}
</style>
